* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  /* background-color: #e4e6eb; */
  color: #2a2a2a;
}

.container {
  max-width: 960px;
  width: 100%;
  /* border: 1px solid #2a2a2a; */
  margin: 0 auto;
}

/********************************************* Header-Nav *********************************************/

header {
  width: 100%;
  background-color: #ba2e1f;
}
.header-nav-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
ul.menu-link {
  display: grid;
  grid-template-columns: 45px 10px 40px 10px 60px 10px 80px;
  padding-left: 0;
  justify-items: center;
}
.menu-link > li {
  list-style: none;
}
.nav-link {
  text-decoration: none;
  font-size: 12px;
  color: #ffffff;
}
.nav-link-seperator {
  text-decoration: none;
  font-size: 12px;
  color: #ffffff;
}
.header-search {
  display: grid;
  justify-items: right;
}

/********************************************* Header-Main *********************************************/

.header-main {
  width: 100%;
}
.header-main-grid {
  height: 93px;
  display: grid;
  /* grid-template-columns: 755px 1fr; */
  grid-template-columns: 1fr 1fr;
}
.header-brand > h1 {
  margin-bottom: 0;
  /* border: red 1px solid; */
  font-size: 36px;
  margin-top: 31px;
}
.header-brand > span {
  font-size: 12px;
}
.social-icon-box {
  display: grid;
  /* grid-template-columns: repeat(5, 1fr); */
  grid-template-columns: 40px 40px 40px 40px 40px;
  /* border: red 1px solid; */
  justify-items: center;
  justify-content: right;
  margin: 0;
}
.social-icon {
  height: 32px;
  padding: 8px;
  background-color: #2a2a2a;
  border-radius: 50%;
  margin-top: 57px;
  transition: 0.3s ease-in-out;
}
.social-icon > a {
  color: white;
}
.social-icon:hover {
  background-color: #ee802f;
}

/********************************************* Main-Nav *********************************************/

.main-nav-grid {
  margin-top: 25px;
  display: grid;
  grid-template-columns: 1fr;
}
ul.main-nav {
  background-color: #2a2a2a;
  border-radius: 5px;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  padding: 16px;
  justify-items: center;
}
ul.main-nav > li {
  list-style: none;
}
ul.main-nav > li > a {
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  font-size: 12px;
}
ul.main-nav > li > a:hover {
  color: #ba2e1f;
}

/********************************************* Section *********************************************/

section {
  width: 100%;
  height: auto;
}

/****** Main Content ******/
.main-content-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: auto;
  gap: 1rem;
}
.img-main-content > img {
  width: 100%;
}
.text-main-content > h1 {
  font-size: 76px;
  margin-bottom: 0;
}
.text-main-content > p {
  margin-top: 0;
  font-size: 16px;
}
.text-main-content > .read-more {
  text-align: right;
}
.text-main-content > .read-more > a {
  color: #ba2e1f;
  text-decoration: none;
}

/****** Service Content ******/
.service {
  background-color: #f2f2f2;
  width: 100%;
}
.service-content-grid {
  background-color: #f2f2f2;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-bottom: 53px;
}

/* service-1 */

.service-1 {
  font-size: 13px;
}
.service-1 > h2 {
  font-size: 26px;
}
.service-1 > .read-more {
  text-align: right;
}
.service-1 > .read-more > a {
  font-size: 13px;
  text-decoration: none;
  color: #ba2e1f;
}

/* service-2 */

.service-2 > h2 {
  font-size: 26px;
}
.service-2 > .read-more {
  text-align: right;
}
.service-2 > .read-more > a {
  font-size: 13px;
  text-decoration: none;
  color: #ba2e1f;
}
.sub-service-2 {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 18px;
  margin-top: 0px;
  margin-bottom: 14px;
}
.sub-service2-content > h3 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 14px;
}
.sub-service2-content > p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 13px;
}

/* service-3 */

.service-3 > h2 {
  font-size: 26px;
}
.sub-service-3 > h3 {
  font-size: 16px;
  margin-top: 0;
  margin-bottom: 0;
}
.sub-service-3 > p {
  font-size: 13px;
  margin-top: 25px;
}
.sub-service-3 > .read-more {
  text-align: right;
  margin-bottom: 36px;
}
.sub-service-3 > .read-more > a {
  font-size: 13px;
  color: #ba2e1f;
  text-decoration: none;
}

/********************************************* Footer *********************************************/

.footer-nav-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
}

/* footer-update */

.footer-update {
  border-top: 1px solid black;
}
.footer-update > h3.footer-title {
  font-size: 16px;
  text-transform: uppercase;
}
.footer-update-tweet > p {
  font-size: 12px;
}
.footer-update-tweet > p > a {
  color: #ba2e1f;
  text-decoration: none;
}

/* footer-link */

.footer-link {
  border-top: 1px solid black;
}
.footer-link > h3.footer-title {
  font-size: 16px;
  text-transform: uppercase;
}
.footer-link > ul {
  padding: 0;
}
.footer-link > ul > li {
  padding: 5px;
  border-bottom: 1px solid gray;
  list-style: none;
}
.footer-link > ul > li > a {
  color: #2a2a2a;
  text-decoration: none;
  font-size: 12px;
}

/* footer-blog */

.footer-blog {
  border-top: 1px solid black;
}
.footer-blog > h3.footer-title {
  font-size: 16px;
}
.footer-blog-box > h4 {
  font-size: 14px;
  margin-top: 0;
  margin-bottom: 0;
}
.footer-blog-box > .read-more {
  padding-bottom: 15px;
  text-align: right;
}
.footer-blog-box > .read-more > a {
  font-size: 14px;
  text-decoration: none;
  color: #ba2e1f;
}
.blog-box-title > p {
  font-size: 10px;
  margin-top: 0;
}
.blog-box-title > p > a {
  color: #ba2e1f;
  text-decoration: none;
  font-size: 10px;
}
.blog-box-detail > p {
  font-size: 14px;
}

/* footer-contact */

.footer-contact {
  border-top: 1px solid black;
}
.footer-contact > h3.footer-title {
  font-size: 16px;
  text-transform: uppercase;
}
.contact-form > form > input {
  height: 30px;
  margin-bottom: 10px;
  width: 100%;
  border: none;
  background-color: #f2f2f2;
  outline: none;
}
.contact-form > form > textarea {
  width: 100%;
  height: 110px;
  margin-bottom: 10px;
  border: none;
  background-color: #f2f2f2;
  outline: none;
}
.contact-form > form > button {
  color: #ba2e1f;
  width: 100%;
  border: none;
  padding: 10px 15px;
  background-color: #f2f2f2;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.contact-form > form > button:hover {
  background-color: #d2d2d2;
}

/********************************************* Footer COPY RIGHT *********************************************/

.footer-copyright {
  height: 65px;
  background-color: #f2f2f2;
}
.footer-copyright-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.footer-copyright-grid > p {
  font-size: 12px;
  margin-top: 26px;
}
.footer-copyright-grid > p:first-child {
  text-align: left;
}
.footer-copyright-grid > p:last-child {
  text-align: right;
}

